export class LoginForm extends Component {
  copy_token(token) {
    if (copy(token))
      alert(
        '\n' +
          process.env.REACT_APP_WEBSITE_URL +
          'token',
      );
  }

  render() {
    return (
      <TokenCtx.Consumer>
        {(token) => (
          <div>
            {/*{!!token.value &&*/}
            {/*    <LifeInfoBox token={token.value} set_token={token.set_value} />*/}
            {/*}*/}
            <div className="login-form box">
              {token.value ? (
                <div>
                  <p>
                    <b></b>
                    <button
                      type="button"
                      onClick={() => {
                        token.set_value(null);
                      }}
                    >
                      <span className="icon icon-logout" /> 
                    </button>
                    <br />
                  </p>
                  {/*<p>*/}
                  {/**/}
                  {/*T(i.e. )<a href="https://github.com/thuhole/thuhole-go-backend/blob/76f56e6b75257b59e552b6bdba77e114151fcad1/src/db.go#L184"></a>*/}
                  {/*</p>*/}
                  <p>
                    <a
                      onClick={() => {
                        this.props.show_sidebar(
                          '',
                          <MessageViewer token={token.value} />,
                        );
                      }}
                    >
                      
                    </a>
                    <br />
                    
                  </p>
                  <p>
                    <a onClick={this.copy_token.bind(this, token.value)}>
                       User Token
                    </a>
                    <br />
                     User Token
                    Token
                    {/* <ResetUsertokenWidget token={token.value} />*/}
                  </p>
                </div>
              ) : (
                <LoginPopup token_callback={token.set_value}>
                  {(do_popup) => (
                    <div>
                      <p>
                        <button type="button" onClick={do_popup}>
                          <span className="icon icon-login" />
                          &nbsp;
                        </button>
                      </p>
                      <p>
                        <small>
                          {process.env.REACT_APP_TITLE}
                          TT
                        </small>
                      </p>
                    </div>
                  )}
                </LoginPopup>
              )}
            </div>
          </div>
        )}
      </TokenCtx.Consumer>
    );
  }
}

